.country-container {
  cursor: pointer; 
}
.country-selectable {
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.country-container:hover .country-selectable{
  fill-opacity: 0.5;
}

.troops-change {
  -webkit-animation: topDisapear 0.75s linear 0s;
          animation: topDisapear 0.75s linear 0s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes topDisapear {
  0% {
    opacity: 1;
  }
  75% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    opacity: 0.75;
  }
  100% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
    display: none;
  }
}

@keyframes topDisapear {
  0% {
    opacity: 1;
  }
  75% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    opacity: 0.75;
  }
  100% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
    display: none;
  }
}


